}

.page-title {
  font-family: Manrope, sans-serif;
  font-weight: 700;
  font-size: 52px;
  line-height: 119%;
}
@media (max-width: 1199px) {
  .page-title {
    font-size: 45px;
  }
}
@media (max-width: 1023px) {
  .page-title {
    font-size: 40px;
