.input-style {
  display: block;
  width: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  padding: 18px 12px;
  border: 1px solid #E8E8E8;
  background-color: transparent;
  margin-top: 10px;
  border-radius: 12px;
  color: #2D373D;
  transition: border-color 0.3s;
}
@media (max-width: 419px) {
  .input-style {
    padding: 12px;
  }
}
.input-style:focus {
  border-color: #2D373D;
}
.input-style.placeholder {
  color: #767676;
}
.input-style::-webkit-input-placeholder {
  color: #767676;
}
.input-style::-moz-placeholder {
  color: #767676;
}
.input-style:-moz-placeholder {
  color: #767676;
}
.input-style:-ms-input-placeholder {
  color: #767676;
}

.select-group {
  position: relative;
}
.select-group .custom-icon {
  position: absolute;
  top: calc(50% - 12px);
  left: 16px;
}
.select-group .jq-selectbox {
  position: relative;
  display: block;
  vertical-align: middle;
  cursor: pointer;
  z-index: 10;
  transition: opacity 0.3s;
}
.select-group .jq-selectbox select {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
}
.select-group .jq-selectbox ul {
  margin: 0;
  padding: 0;
}
.select-group .jq-selectbox li {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  white-space: nowrap;
  padding: 10px;
  color: #2D373D;
}
.select-group .jq-selectbox li.selected {
  background-color: #A3ABB1;
  color: #FFF;
}
.select-group .jq-selectbox li:hover {
  background-color: #116893;
  color: #FFF;
}
.select-group .jq-selectbox li.disabled {
  color: #AAA;
}
.select-group .jq-selectbox li.disabled:hover {
  background: none;
}
.select-group .jq-selectbox li.optgroup {
  font-weight: bold;
}
.select-group .jq-selectbox li.optgroup:hover {
  background: none;
  color: #231F20;
  cursor: default;
}
.select-group .jq-selectbox li.option {
  padding-left: 25px;
}
.select-group .jq-selectbox__select {
  position: relative;
  padding: 0 45px 0 52px;
  font-weight: 500;
  border: 1px solid #E8E8E8;
  border-radius: 8px;
  transition: border-color 0.3s;
}
.select-group .jq-selectbox__select-text {
  overflow: hidden;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
  width: 100%;
  color: #767676;
  transition: color 0.3s;
  padding: 11px 0;
}
@media (max-width: 419px) {
  .select-group .jq-selectbox__select-text {
    font-size: 14px;
    line-height: 20px;
  }
}
.select-group .jq-selectbox__dropdown {
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  margin: 2px 0 0;
  padding: 0;
  border: 1px solid #E8E8E8;
  border-radius: 4px;
  overflow: hidden;
  background: #FFF;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
}
.select-group .jq-selectbox__dropdown ul {
  position: relative;
  overflow: auto;
  overflow-x: hidden;
  list-style: none;
  -webkit-overflow-scrolling: touch;
}
.select-group .jq-selectbox.focused .jq-selectbox__select {
  border-color: #2D373D;
}
.select-group .jq-selectbox.disabled {
  opacity: 0.5;
  cursor: default;
}
.select-group .jq-selectbox__trigger {
  position: absolute;
  top: 0;
  right: 0;
  width: 42px;
  height: 100%;
}
.select-group .jq-selectbox__trigger-arrow:before {
  content: "\e80a";
  color: #116893;
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: block;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  font-size: 12px;
  line-height: 12px;
  position: absolute;
  top: calc(50% - 6px);
  right: calc(50% - 6px);
}
.select-group .jq-selectbox.changed .jq-selectbox__select-text {
  color: #2D373D;
}

.radio-group label {
  display: block;
  position: relative;
}
.radio-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.radio-group label input:checked + .label:before {
  background-color: #116893;
  color: #fff;
  border-color: #116893;
}
.radio-group label .label {
  font-weight: 600;
  padding-left: 35px;
  display: inline-block;
  position: relative;
}
.radio-group label .label:before {
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  content: "\e800";
  width: 24px;
  height: 24px;
  font-size: 11px;
  line-height: 11px;
  border-radius: 50%;
  background-color: transparent;
  color: transparent;
  border: 1px solid #2D373D;
  position: absolute;
  padding-top: 1px;
  top: 0;
  left: 0;
  transition: color 0.3s, background-color 0.3s, border-color 0.3s;
}
.radio-group .radio-descr {
  margin-top: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #767676;
}
@media (max-width: 419px) {
  .radio-group .radio-descr {
    font-size: 14px;
    line-height: 20px;
  }
}

.textarea-group label {
  display: block;
}
.textarea-group label .label {
  font-weight: 600;
}
.textarea-group label textarea {
  min-height: 160px;
}
@media (max-width: 419px) {
  .textarea-group label textarea {
    font-size: 14px;
    line-height: 20px;
  }
}

.file-group label {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}
.file-group .jq-file {
  position: relative;
  display: inline-block;
  overflow: hidden;
  width: 100%;
  border-radius: 8px;
  border: 1px solid #E8E8E8;
  transition: border-color 0.3s;
  cursor: pointer;
}
.file-group .jq-file input {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  width: 0;
  height: 0;
  cursor: pointer;
  z-index: 1;
}
.file-group .jq-file.focused {
  border-color: #2D373D;
}
.file-group .jq-file.changed .jq-file__name {
  color: #2D373D;
}
.file-group .jq-file.changed .icon.icon-file:before {
  content: "\e802";
}
.file-group .jq-file:hover {
  border-color: #2D373D;
}
.file-group .jq-file__name {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  box-sizing: border-box;
  width: 100%;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  padding: 13px 80px 13px 15px;
  color: #767676;
  transition: color 0.3s;
  cursor: pointer;
}
.file-group .jq-file__browse {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  width: 54px;
  height: 100%;
  color: #116893;
  font-size: 24px;
  line-height: 24px;
  cursor: pointer;
}

.check-group label {
  display: inline-flex;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  min-height: 24px;
}
.check-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.check-group label input:checked + .label:before {
  background-color: #116893;
  color: #fff;
  border-color: #116893;
}
.check-group label .label {
  display: block;
  padding-left: 35px;
  color: #767676;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  position: relative;
}
@media (max-width: 575px) {
  .check-group label .label {
    font-size: 14px;
  }
}
.check-group label .label:before {
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  content: "\e800";
  width: 24px;
  height: 24px;
  font-size: 11px;
  line-height: 11px;
  border-radius: 4px;
  background-color: transparent;
  color: transparent;
  border: 1px solid #2D373D;
  position: absolute;
  padding-top: 1px;
  top: 0;
  left: 0;
  transition: color 0.3s, background-color 0.3s, border-color 0.3s;
}
.check-group label .label a {
  color: #116893;
  text-decoration: underline;
}
.check-group label .label a:hover {
  text-decoration: none;
}

.date-group {
  display: block;
  position: relative;
}
.date-group .custom-icon {
  position: absolute;
  top: calc(50% - 12px);
  left: 15px;
}
.date-group .input-style {
  padding: 12px 12px 12px 50px;
  font-weight: 500;
  color: transparent;
  border-radius: 8px;
  -webkit-appearance: none;
  transition: color 0.3s, border-color 0.3s;
  cursor: pointer;
}
.date-group .input-style:before {
  content: attr(placeholder);
  color: #767676;
  position: absolute;
  width: 100%;
  max-width: calc(100% - 52px);
  background-color: #fff;
  font-size: 18px;
  line-height: 24px;
}
@media (max-width: 575px) {
  .date-group .input-style:before {
    font-size: 16px;
  }
}
@media (max-width: 419px) {
  .date-group .input-style:before {
    font-size: 14px;
  }
}
.date-group .input-style.changed {
  color: #2D373D;
}
.date-group .input-style.changed:before {
  display: none;
}
.date-group .input-style::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}

