@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"
/* main page */
.video-answers
  margin-top: 120px
  @media (max-width: 1335px)
    margin-top: 75px
  @media (max-width: 1199px)
    margin-top: 55px
  &__content
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 45px
    @media (max-width: 419px)
      margin-top: 25px
    &__item
      flex: 0 0 calc(50% - 20px)
      max-width: calc(50% - 20px)
      margin: 0 40px 40px 0
      border-radius: 24px
      overflow: hidden
      &:nth-of-type(2n)
        margin-right: 0
      @media (max-width: 1199px)
        flex: 0 0 calc(50% - 15px)
        max-width: calc(50% - 15px)
        margin: 0 30px 30px 0
      @media (max-width: 959px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin: 0 20px 20px 0
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0 0 20px 0
      .video-link
        display: block
        position: relative
        &:after
          content: '\e808'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          position: absolute
          top: calc(50% - 40px)
          left: calc(50% - 40px)
          font-size: 80px
          line-height: 80px
          color: rgba(230, 233, 242, 0.8)
          transition: transform .3s
          @media (max-width: 1199px)
            font-size: 60px
            line-height: 60px
            top: calc(50% - 30px)
            left: calc(50% - 30px)
          @media (max-width: 767px)
            font-size: 48px
            line-height: 48px
            top: calc(50% - 24px)
            left: calc(50% - 24px)
        &:hover
          &:after
            transform: scale(1.1)
      .item-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
    &__btns
      display: flex
      align-items: center
      justify-content: flex-start
      flex-wrap: wrap
      flex: 0 0 100%
      max-width: 100%
      .answers-btn
        flex: 0 0 calc(50% - 20px)
        max-width: calc(50% - 20px)
        padding: 22px
        @media (max-width: 1199px)
          flex: 0 0 calc(50% - 15px)
          max-width: calc(50% - 15px)
          font-size: 22px
        @media (max-width: 959px)
          flex: 0 0 calc(50% - 10px)
          max-width: calc(50% - 10px)
          font-size: 18px
          padding: 15px
        @media (max-width: 575px)
          flex: 0 0 100%
          max-width: 100%
        &.see-all
          margin-right: 40px
          @media (max-width: 1199px)
            margin-right: 30px
          @media (max-width: 959px)
            margin-right: 20px
          @media (max-width: 575px)
            margin-right: 0
            margin-bottom: 20px

.sample-examples
  margin-top: 75px
  background-color: #fff
  padding-top: 75px
  @media (max-width: 1199px)
    padding-top: 45px
  @media (max-width: 959px)
    margin-top: 55px
  @media (max-width: 575px)
    position: relative
    padding-bottom: 95px
  .title-row
    .btn-block
      margin: 0 5px 0 auto
      @media (max-width: 575px)
        position: absolute
        bottom: 25px
        left: 0
        right: 0
        margin: 0 auto
        max-width: 280px
      .backcall-btn
        display: block
        padding-left: 85px
        position: relative
        @media (max-width: 767px)
          padding-left: 75px
        &:before
          display: flex
          align-items: center
          justify-content: center
          width: 70px
          height: 70px
          border: 1px solid $border-gray-color
          border-radius: 8px
          content: url("../images/common/svg/icons/phone.svg")
          position: absolute
          top: 0
          left: 0
          @media (max-width: 767px)
            transform: scale(.8)
            left: -7px
        p
          color: $txt-color
          font-weight: 600
          font-size: 24px
          line-height: 33px
          @media (max-width: 959px)
            font-size: 20px
            line-height: 28px
            padding-top: 5px
        span
          display: inline-block
          font-weight: 500
          font-size: 18px
          line-height: 25px
          text-decoration: underline
          color: $accent-color
          margin-top: 5px
          @media (max-width: 959px)
            font-size: 16px
            line-height: 20px
        &:hover
          span
            text-decoration: none
  &__content
    margin: 45px 0
    @media (max-width: 575px)
      margin: 25px 0 0
    .examples-slider
      position: relative
      .slide-item
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        background-color: $bg-color
        padding: 30px 20px
        border-radius: 32px
        border: 1px solid $border-gray-color
        width: calc(60% + 12px)
        max-width: 1160px
        @media (max-width: 1199px)
          width: 80%
        @media (max-width: 1023px)
          width: 90%
        @media (max-width: 767px)
          flex-direction: column
          align-items: flex-start
          justify-content: flex-start
          max-width: 80%
        @media (max-width: 575px)
          max-width: 100%
          border-radius: 24px
          padding: 10px
        .slide-image
          display: block
          flex: 0 0 50%
          max-width: 50%
          border-radius: 24px
          @media (max-width: 767px)
            flex: 0 0 auto
            max-width: 100%
      .slider-navigation
        display: flex
        align-items: center
        justify-content: flex-start
        position: absolute
        top: calc(50% - 28px)
        height: 0
        left: 0
        right: 0
        margin: 0 auto
        max-width: 1220px
        z-index: 1
        @media (max-width: 1335px)
          max-width: calc(100% - 30px)
          left: 15px
        @media (max-width: 575px)
          left: 0
          max-width: 100%
        .nav-btn
          display: flex
          align-items: center
          justify-content: center
          width: 60px
          height: 60px
          border: 1px solid $border-gray-color
          border-radius: 50%
          color: $accent-color
          font-size: 24px
          line-height: 24px
          transition: color .3s, background-color .3s, border-color .3s
          padding-left: 2px
          background-color: #fff
          @media (max-width: 575px)
            width: 50px
            height: 50px
            font-size: 20px
            line-height: 20px
          &:hover
            color: #fff
            border-color: $accent-color
            background-color: $accent-color
          &.prev-btn
            margin: 0 auto 0 -50px
            transform: scale(-1,1)
            @media (max-width: 1335px)
              margin: 0 auto 0 0
          &.next-btn
            margin: 0 -50px 0 auto
            @media (max-width: 1335px)
              margin: 0 0 0 auto
      .slider-pagination
        display: flex
        align-items: center
        justify-content: center
        background-color: #fff
        padding: 20px 0
        .swiper-pagination-bullet
          background-color: $border-gray-color
          width: 12px
          height: 12px
          margin: 0 7px
          opacity: 1
          transition: background-color .3s
          &.swiper-pagination-bullet-active
            background-color: $accent-color

.advantages-block
  background-color: #fff
  padding: 60px 0
  @media (max-width: 1335px)
    padding: 30px 0
  .title-row
    .descr-block
      max-width: 310px
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        margin-top: 15px 0 0
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 45px
    @media (max-width: 767px)
      margin-top: 25px
    &__item
      flex: 0 0 calc(33.3% - 27px)
      max-width: calc(33.3% - 27px)
      margin: 0 40px 38px 0
      background-color: $bg-color
      border: 1px solid $border-gray-color
      border-radius: 24px
      padding: 30px 24px
      @media (max-width: 1199px)
        flex: 0 0 calc(33.3% - 14px)
        max-width: calc(33.3% - 14px)
        margin: 0 20px 20px 0
        padding: 20px
      @media (max-width: 959px)
        padding: 15px
        border-radius: 12px
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0 0 20px 0
      &:nth-of-type(3n),
      &:nth-of-type(3n + 8)
        margin-right: 0
      &.half-item
        display: flex
        align-items: stretch
        justify-content: flex-start
        flex: 0 0 calc(50% - 20px)
        max-width: calc(50% - 20px)
        margin: 0 40px 40px 0
        padding: 0
        color: #fff
        background-color: $accent-color
        overflow: hidden
        border: none
        @media (max-width: 1199px)
          flex: 0 0 calc(50% - 10px)
          max-width: calc(50% - 10px)
          margin: 0 20px 20px 0
        @media (max-width: 959px)
          flex: 0 0 100%
          max-width: 100%
          margin: 0 0 20px 0
        & + .advantages-block__content__item.half-item
          background-color: $txt-color
          margin-right: 0
          & + .advantages-block__content__item
            margin-right: 40px
            @media (max-width: 1199px)
              margin-right: 20px
        .descr-block
          flex: 0 0 70%
          max-width: 70%
          margin-top: 0
          padding: 30px 25px 42px
          @media (max-width: 1199px)
            padding: 30px 25px
          @media (max-width: 1023px)
            flex: 0 0 75%
            max-width: 75%
          @media (max-width: 959px)
            padding: 20px 15px
          @media (max-width: 419px)
            flex: 0 0 85%
            max-width: 85%
          h2
            font-weight: 600
            font-size: 24px
            line-height: 30px
            @media (max-width: 1023px)
              font-size: 22px
            @media (max-width: 359px)
              font-size: 20px
          p
            display: inline-block
            margin-top: 45px
            font-weight: 500
            font-size: 18px
            line-height: 25px
            @media (max-width: 1199px)
              margin-top: 25px
            @media (max-width: 1023px)
              margin-top: 15px
            @media (max-width: 959px)
              margin-top: 45px
            @media (max-width: 767px)
              max-width: 320px
              margin-top: 25px
            @media (max-width: 575px)
              font-size: 16px
            @media (max-width: 359px)
              font-size: 15px
              line-height: 22px
        .image-block
          flex: 0 0 30%
          max-width: 30%
          position: relative
          @media (max-width: 1023px)
            flex: 0 0 25%
            max-width: 25%
          @media (max-width: 419px)
            flex: 0 0 40%
            max-width: 40%
            margin-left: -10%
          .item-icon
            display: block
            width: auto
            max-width: 100%
            position: absolute
            top: 0
            right: 0
      .icon-block
        display: flex
        align-items: center
        justify-content: center
        width: 70px
        height: 70px
        background-color: #fff
        border: 1px solid $border-gray-color
        border-radius: 8px
        @media (max-width: 959px)
          width: 60px
          height: 60px
          padding: 8px
        .item-icon
          display: block
          width: auto
          max-width: 100%
          margin: 0 auto
      .descr-block
        margin-top: 30px
        font-weight: 600
        font-size: 24px
        line-height: 125%
        letter-spacing: -.02em
        @media (max-width: 1199px)
          font-size: 20px
          margin-top: 20px
        @media (max-width: 1023px)
          font-size: 18px
        @media (max-width: 959px)
          font-size: 16px
        @media (max-width: 767px)
          font-size: 18px
        @media (max-width: 575px)
          max-width: 275px
.main-legal
  background-color: #fff
  padding: 20px 0
  &__title
    margin-top: 30px
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    background-color: $bg-color
    border: 1px solid $border-gray-color
    border-radius: 32px
    padding: 30px 22px
    @media (max-width: 1199px)
      align-items: flex-end
    @media (max-width: 767px)
      padding: 20px 15px
      border-radius: 24px
    &__left-side
      flex: 0 0 calc(50% - 20px)
      max-width: calc(50% - 20px)
      margin-right: 40px
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        margin-right: 0
      .tag
        display: inline-block
        text-transform: uppercase
        color: $txt-gray-color
        font-size: 15px
        line-height: 18px
        padding: 7px 25px
        border: 1px solid $border-gray-color
        border-radius: 8px
        @media (max-width: 959px)
          font-size: 13px
          padding: 7px 15px
        @media (max-width: 419px)
          display: block
          text-align: center
      .descr-block
        margin-top: 30px
        p
          margin-top: 10px
          &:nth-of-type(1)
            margin-top: 0
    &__right-side
      flex: 0 0 calc(50% - 20px)
      max-width: calc(50% - 20px)
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        margin-top: 25px
      .image-block
        .legal-video-link
          display: block
          position: relative
          &:after
            content: '\e808'
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            position: absolute
            top: calc(50% - 40px)
            left: calc(50% - 40px)
            font-size: 80px
            line-height: 80px
            color: rgba(230, 233, 242, 0.8)
            transition: transform .3s
            @media (max-width: 1199px)
              font-size: 60px
              line-height: 60px
              top: calc(50% - 30px)
              left: calc(50% - 30px)
            @media (max-width: 575px)
              font-size: 48px
              line-height: 48px
              top: calc(50% - 24px)
              left: calc(50% - 24px)
          &:hover
            &:after
              transform: scale(1.1)
          .item-image
            display: block
            width: auto
            max-width: 100%
            margin: 0
            border-radius: 24px
            @media (max-width: 767px)
              width: 100%
.order-block
  background-color: #fff
  padding: 100px 0 80px
  @media (max-width: 1335px)
    padding: 55px 0 75px
  @media (max-width: 1199px)
    padding: 55px 0
  @media (max-width: 959px)
    padding: 35px 0 55px
  &__title
    margin-top: 20px
  &__subtitle
    margin-top: 30px
    font-weight: 600
    font-size: 24px
    line-height: 33px
    @media (max-width: 959px)
      font-size: 20px
      line-height: 28px
      margin-top: 20px
    @media (max-width: 575px)
      font-weight: 500
      font-size: 16px
      line-height: 24px
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    background-color: $txt-color
    border-radius: 32px
    width: calc(100% + 160px)
    margin: 0 -80px
    padding: 60px 80px
    position: relative
    overflow: hidden
    @media (max-width: 1335px)
      width: 100%
      margin: 0 auto
      padding: 40px
    @media (max-width: 959px)
      padding: 30px 20px
      border-radius: 24px
    @media (max-width: 575px)
      padding: 100px 20px 30px
    &__left-side
      flex: 0 0 60%
      max-width: 60%
      color: #fff
      @media (max-width: 1023px)
        flex: 0 0 70%
        max-width: 70%
      @media (max-width: 959px)
        flex: 0 0 75%
        max-width: 75%
        position: relative
        z-index: 1
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
      .tag-row
        display: flex
        align-items: center
        justify-content: flex-start
        @media (max-width: 575px)
          flex-wrap: wrap
        .tag
          display: inline-block
          color: #fff
          text-transform: uppercase
          font-size: 15px
          line-height: 18px
          padding: 9px 24px
          border: 1px solid #fff
          border-radius: 8px
          @media (max-width: 959px)
            font-size: 13px
            padding: 7px 15px
        .time
          display: inline-flex
          align-items: center
          justify-content: center
          margin-left: 40px
          color: #fff
          font-size: 15px
          line-height: 24px
          @media (max-width: 575px)
            order: -1
            margin-left: 0
            margin-bottom: 15px
          .icon
            margin-right: 12px
            font-size: 24px
            line-height: 24px
      .order-link
        margin-top: 40px
        max-width: 360px
        @media (max-width: 959px)
          margin-top: 25px
          font-size: 18px
          max-width: 320px
        @media (max-width: 575px)
          max-width: 100%
    &__right-side
      flex: 0 0 40%
      max-width: 40%
      @media (max-width: 1199px)
        position: relative
      @media (max-width: 1023px)
        flex: 0 0 30%
        max-width: 30%
      @media (max-width: 959px)
        flex: 0 0 auto
        width: auto
        bottom: 0
        right: 0
        position: absolute
      @media (max-width: 767px)
        bottom: -60px
      @media (max-width: 575px)
        bottom: auto
        top: 0
        right: 0
        max-width: 45%
      .order-image
        display: block
        width: auto
        max-width: 100%
        margin: 0 auto
        position: absolute
        bottom: 0
        right: 0
        @media (max-width: 1199px)
          max-width: 100%
          right: -40px
          bottom: -40px
        @media (max-width: 1023px)
          max-width: calc(100% + 40px)
        @media (max-width: 959px)
          position: relative
          right: 0
          bottom: 0
        @media (max-width: 575px)
          max-width: 100%
.main-drawing
  padding: 75px 0
  @media (max-width: 1199px)
    padding: 50px 0
  &__content
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    position: relative
    &__left-side
      flex: 0 0 calc(50% - 20px)
      max-width: calc(50% - 20px)
      margin: 0 40px 0 0
      position: sticky
      top: 160px
      left: 0
      @media (max-width: 1199px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin: 0 20px 0 0
      @media (max-width: 959px)
        flex: 0 0 calc(40% - 10px)
        max-width: calc(40% - 10px)
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0
        position: relative
        top: 0
    &__right-side
      flex: 0 0 calc(50% - 20px)
      max-width: calc(50% - 20px)
      @media (max-width: 1199px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
      @media (max-width: 959px)
        flex: 0 0 calc(60% - 10px)
        max-width: calc(60% - 10px)
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        margin-top: 25px
      .descr-item
        background-color: #fff
        border: 1px solid $border-gray-color
        border-radius: 24px
        padding: 30px 25px
        margin-top: 40px
        @media (max-width: 1199px)
          margin-top: 20px
        @media (max-width: 959px)
          padding: 30px 20px
        &:nth-of-type(1)
          margin-top: 0
        .image-block
          display: flex
          align-items: flex-start
          justify-content: flex-start
          flex-wrap: wrap
          .icon-block
            display: inline-flex
            align-items: center
            justify-content: center
            background-color: #fff
            border: 1px solid $border-gray-color
            border-radius: 8px
            width: 68px
            height: 68px
            flex: 0 0 68px
            @media (max-width: 959px)
              width: 60px
              height: 60px
              flex: 0 0 60px
              padding: 8px
          .icon
            display: block
            width: auto
            max-width: 100%
            margin: 0 auto
          .item-tag
            display: inline-block
            background-color: $accent-color
            color: #fff
            text-transform: uppercase
            border-radius: 8px
            padding: 9px 25px
            font-size: 15px
            line-height: 20px
            margin: 0 0 0 auto
            @media (max-width: 959px)
              font-size: 13px
              padding: 7px 15px
        .descr-block
          margin-top: 30px
          letter-spacing: .01em
          @media (max-width: 1199px)
            margin-top: 20px
  &__slider
    margin-top: 40px
    display: flex
    align-items: flex-start
    justify-content: flex-start
    position: relative
    @media (max-width: 959px)
      flex-wrap: wrap
    .descr-side
      flex: 0 0 calc(50% - 20px)
      max-width: calc(50% - 20px)
      margin-right: 40px
      @media (max-width: 1199px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin-right: 20px
      @media (max-width: 959px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0
      .descr-block
        background-color: #fff
        border: 1px solid $border-gray-color
        border-radius: 24px
        padding: 30px 24px
        @media (max-width: 1199px)
          padding: 24px
        @media (max-width: 959px)
          padding: 30px 20px
        .item-title
          display: inline-block
          text-transform: uppercase
          color: $txt-gray-color
          font-size: 15px
          line-height: 18px
          border: 1px solid $border-gray-color
          border-radius: 8px
          padding: 7px 25px
          @media (max-width: 959px)
            font-size: 13px
            padding: 7px 15px
          @media (max-width: 419px)
            display: block
            text-align: center
        ul
          margin: 21px 0 0
          padding: 0
          list-style-type: none
          letter-spacing: .01em
          @media (max-width: 1199px)
            margin-top: 15px
          @media (max-width: 959px)
            margin-top: 20px
          li
            display: block
            position: relative
            padding-left: 35px
            margin-top: 15px
            @media (max-width: 1199px)
              margin-top: 12px
            @media (max-width: 959px)
              margin-top: 20px
            &:nth-of-type(1)
              margin-top: 0
            &:before
              display: block
              content: url("../images/common/svg/icons/check_circle.svg")
              position: absolute
              top: 0
              left: 0
