.footer
  background-color: #fff
  border-radius: 100px 100px 0 0
  width: 100%
  max-width: calc(100% - 80px)
  margin: 120px auto 0
  @media (max-width: 1335px)
    margin: 75px auto 0
    max-width: 100%
    border-radius: 50px 50px 0 0
  @media (max-width: 1199px)
    margin: 55px auto 0
  @media (max-width: 959px)
    border-radius: 25px 25px 0 0
  &__content
    padding: 75px 0
    @media (max-width: 1335px)
      padding: 50px 0
    @media (max-width: 1199px)
      padding: 30px 0
    &__top
      display: flex
      align-items: center
      justify-content: flex-start
      padding-bottom: 45px
      border-bottom: 1px solid $border-gray-color
      @media (max-width: 1199px)
        padding-bottom: 25px
      .logo-link
        display: block
        width: 200px
        @media (max-width: 1199px)
          width: 155px
        @media (max-width: 419px)
          width: 120px
        .logo-image
          display: block
          width: 100%
      .btns-row
        display: flex
        align-items: center
        justify-content: flex-start
        margin: 0 0 0 auto
        a
          display: flex
          align-items: center
          justify-content: center
          flex: 0 0 70px
          width: 70px
          height: 70px
          border: 1px solid $border-gray-color
          border-radius: 50%
          @media (max-width: 959px)
            flex: 0 0 50px
            width: 50px
            height: 50px
          .icon
            transition: transform .3s
          &:hover
            .icon
              transform: scale(1.1)
          &.phone-link
            color: $accent-color
            font-size: 28px
            line-height: 28px
            @media (max-width: 959px)
              font-size: 22px
              line-height: 22px
          &.wa-link
            margin-left: 20px
            color: #39AE41
            font-size: 30px
            line-height: 30px
            @media (max-width: 959px)
              font-size: 24px
              line-height: 24px
    &__bottom
      display: flex
      align-items: stretch
      justify-content: flex-start
      flex-wrap: wrap
      margin-top: 45px
      @media (max-width: 1199px)
        margin-top: 25px
      .column-title
        display: inline-block
        font-weight: 700
        font-size: 18px
        line-height: 25px
        color: $accent-color
        margin-bottom: 32px
        @media (max-width: 419px)
          margin-bottom: 15px
      .info-column
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        color: $txt-gray-color
        font-weight: 500
        font-size: 16px
        line-height: 22px
        max-width: 360px
        flex: 0 0 360px
        @media (max-width: 767px)
          flex: 0 0 100%
          max-width: 100%
        @media (max-width: 419px)
          font-size: 15px
        .info-block
          font-weight: 500
          font-size: 18px
          line-height: 25px
          margin-top: 45px
          @media (max-width: 1199px)
            margin-top: 25px
          @media (max-width: 419px)
            font-size: 16px
            margin-top: 15px
          span
            display: inline-block
            font-size: 15px
            line-height: 20px
            margin: 10px 0 45px
            padding-left: 18px
            position: relative
            @media (max-width: 1199px)
              margin: 10px 0 25px
            @media (max-width: 419px)
              margin: 10px 0 15px
            &:before
              content: ''
              display: block
              width: 7px
              height: 7px
              border-radius: 50%
              background-color: $txt-green-color
              position: absolute
              top: 7px
              left: 0
        .copyright
          display: inline-block
          font-weight: 400
          font-size: 16px
          line-height: 22px
          color: $txt-gray-color
          margin-top: auto

      .contacts-column
        margin: 0 auto
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        padding-left: 22px
        @media (max-width: 767px)
          flex: 0 0 100%
          max-width: 100%
          padding-left: 0
          margin-top: 30px
        a
          display: inline-block
          font-family: Manrope, sans-serif
          font-weight: 700
          font-size: 42px
          line-height: 119%
          color: $txt-color
          transition: color .3s
          @media (max-width: 1199px)
            font-size: 35px
          @media (max-width: 1023px)
            font-size: 30px
          @media (max-width: 575px)
            font-size: 26px
          &:hover
            color: $accent-hover-color
          &.mail-link
            margin: 15px 0
          &.privacy-link
            font-weight: 400
            font-size: 16px
            line-height: 22px
            color: $txt-gray-color
            margin-top: auto
            transition: color .3s
            &:hover
              color: $accent-color
      .map-column
        flex: 0 0 190px
        max-width: 190px
        margin: 0 0 0 auto
        @media (max-width: 959px)
          flex: 0 0 100%
          max-width: 100%
          margin: 30px 0 0
        .column-title
          @media (max-width: 959px)
            margin-bottom: 15px
        .footer-menu
          &__list
            @media (max-width: 959px)
              display: flex
              align-items: center
              justify-content: flex-start
              flex-wrap: wrap
          &__item
            margin-top: 22px
            @media (max-width: 1199px)
              margin-top: 15px
            @media (max-width: 959px)
              margin: 0 18px 20px 0
            @media (max-width: 767px)
              margin: 0 15px 15px 0
              min-width: calc(25% - 25px)
            @media (max-width: 575px)
              min-width: calc(50% - 10px)
              margin: 0 20px 15px 0
              &:nth-of-type(2n)
                margin-right: 0
              &:last-child
                min-width: 100%
                margin: 0
            @media (max-width: 419px)
              min-width: 25%
            @media (max-width: 359px)
              margin: 0 18px 15px 0
            &:nth-of-type(1)
              margin-top: 0
          &__link
            display: inline-block
            color: $txt-gray-color
            border: 1px solid $border-gray-color
            border-radius: 8px
            font-weight: 500
            font-size: 15px
            line-height: 18px
            padding: 5px 14px
            transition: background-color .3s, color .3s, border-color .3s
            @media (max-width: 767px)
              display: block
              width: 100%
              text-align: center
              padding: 8px 15px
            &:hover
              background-color: $accent-color
              color: #fff
              border-color: $accent-color
            &.btn-style
              display: inline-flex
              align-items: center
              justify-content: flex-start
              color: #fff
              padding: 8px 15px 8px 24px
              border: none
              @media (max-width: 575px)
                align-items: center
                justify-content: center
                padding: 15px
              .icon
                font-size: 12px
                line-height: 12px
                margin-left: 12px
                transition: transform .3s
              &:hover
                background-color: $accent-hover-color
                .icon
                  transform: translateX(3px)
      .mobile-footer-column
        @media (max-width: 959px)
          display: flex
          align-items: center
          justify-content: flex-start
          flex-wrap: wrap
          flex: 0 0 100%
          width: 100%
          margin-top: 20px
          .copyright
            display: inline-block
            font-weight: 400
            font-size: 16px
            line-height: 22px
            color: $txt-gray-color
            @media (max-width: 767px)
              font-size: 14px
          .privacy-link
            display: inline-block
            font-weight: 400
            font-size: 16px
            line-height: 22px
            color: $txt-gray-color
            margin: 0 0 0 auto
            transition: color .3s
            @media (max-width: 767px)
              font-size: 14px
            &:hover
              color: $accent-color
        @media (max-width: 575px)
          flex-direction: column
          align-items: flex-start
          justify-content: flex-start
          .privacy-link
            margin: 10px 0 0 0


