.mfp-fade
  &.mfp-bg
    opacity: 0
    -webkit-transition: all 0.15s ease-out
    -moz-transition: all 0.15s ease-out
    transition: all 0.15s ease-out
    &.mfp-ready
      opacity: 0.8
    &.mfp-removing
      opacity: 0
  &.mfp-wrap
    @include scrollbars($scrl-bar_width, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
    .mfp-content
      opacity: 0
      -webkit-transition: all 0.15s ease-out
      -moz-transition: all 0.15s ease-out
      transition: all 0.15s ease-out
    &.mfp-ready
      .mfp-content
        opacity: 1
    &.mfp-removing
      .mfp-content
        opacity: 0
.simple-modal
  max-width: 640px
  background-color: #fff
  border-radius: 24px
  padding: 30px 24px
  margin: 60px auto
  position: relative
  .mfp-close
    width: 32px
    height: 32px
    display: flex
    align-items: center
    justify-content: center
    opacity: 1
    font-size: 30px
    line-height: 30px
    top: 35px
    right: 30px
    @media (max-width: 575px)
      top: 27px
    &:after
      font-family: Fontello
      font-style: normal
      font-weight: normal
      speak: never
      display: block
      margin: 0
      text-decoration: inherit
      text-align: center
      font-variant: normal
      text-transform: none
      content: '\e801'
  .modal-title
    font-family: Manrope, sans-serif
    font-weight: 600
    font-size: 32px
    line-height: 125%
    padding-right: 80px
    @media (max-width: 575px)
      font-size: 26px
      line-height: 26px
  &.price-modal
    max-width: 1320px
    padding: 60px 80px
    border-radius: 32px
    @media (max-width: 1335px)
      padding: 40px
    @media (max-width: 767px)
      padding: 20px
      border-radius: 24px
    @media (max-width: 419px)
      padding: 20px 15px
      border-radius: 12px
    .mfp-close
      width: 40px
      height: 40px
      font-size: 36px
      line-height: 36px
      top: 70px
      right: 85px
      @media (max-width: 1199px)
        top: 50px
        right: 60px
      @media (max-width: 1023px)
        top: 42px
      @media (max-width: 767px)
        top: 22px
        right: 30px
      @media (max-width: 419px)
        font-size: 30px
        line-height: 30px
        width: 30px
        height: 30px
        top: 24px
    .modal-title
      font-weight: 700
      font-size: 52px
      line-height: 119%
      @media (max-width: 1023px)
        font-size: 40px
      @media (max-width: 419px)
        font-size: 32px
    .modal-content
      margin-top: 45px
      @media (max-width: 767px)
        margin-top: 25px
      .price-row
        margin-top: 30px
        &:nth-of-type(1)
          margin-top: 0
        .row-title
          font-weight: 700
          font-size: 32px
          line-height: 125%
          @media (max-width: 1023px)
            font-size: 28px
          @media (max-width: 767px)
            font-size: 24px
        .price-list
          display: flex
          align-items: flex-start
          justify-content: flex-start
          flex-wrap: wrap
          margin-top: 20px
          &__item
            display: flex
            align-items: center
            justify-content: flex-start
            flex-wrap: wrap
            flex: 0 0 calc(50% - 5px)
            max-width: calc(50% - 5px)
            margin: 0 10px 10px 0
            background-color: $bg-color
            border-radius: 12px
            border: 1px solid $border-gray-color
            padding: 17px 20px
            font-weight: 500
            @media (max-width: 1199px)
              flex: 0 0 100%
              max-width: 100%
            @media (max-width: 575px)
              padding: 5px
            &:nth-of-type(2n)
              margin-right: 0
            span
              display: inline-block
              @media (max-width: 757px)
                padding: 5px
              &:nth-of-type(2)
                margin-left: auto
                font-weight: 700
      .price-info
        margin-top: 20px
        font-weight: 500
        b,strong
          font-weight: 600
  &.reg-modal
    .reg-menu
      margin-top: 30px
      display: flex
      align-items: center
      justify-content: flex-start
      flex-wrap: wrap
      .modal-btn
        display: block
        width: 100%
        text-align: center
        border: 1px solid $border-gray-color
        border-radius: 8px
        font-weight: 500
        color: $txt-color
        background-color: transparent
        flex: 0 0 calc(50% - 15px)
        max-width: calc(50% - 15px)
        padding: 11px
        transition: background-color .3s, color .3s, border-color .3s
        @media (max-width: 575px)
          flex: 0 0 100%
          max-width: 100%
        &:nth-of-type(2)
          margin-left: 30px
          @media (max-width: 575px)
            margin: 15px 0 0 0
        &:hover, &.active
          background-color: $accent-color
          color: #fff
          border-color: $accent-color
    .reg-content
      display: none
      &.active
        display: block
      .legal-form
        margin-top: 30px
        .input-group
          margin-top: 30px
          &:nth-of-type(1)
            margin-top: 0
          .label
            font-weight: 500
          button
            display: block
            width: 100%
        .input-row
          display: flex
          align-items: center
          justify-content: flex-start
          flex-wrap: wrap
          margin-top: 30px
          .input-group
            flex: 0 0 calc(50% - 15px)
            max-width: calc(50% - 15px)
            margin-top: 0
            @media (max-width: 575px)
              flex: 0 0 100%
              max-width: 100%
            &:nth-of-type(2)
              margin-left: 30px
              @media (max-width: 575px)
                margin: 25px 0 0 0
      .individual-descr
        margin: 30px 0
        font-weight: 500
        p
          margin-top: 15px
          &:nth-of-type(1)
            margin-top: 0
        b,strong
          font-weight: 600
  &.login-modal
    .login-form
      margin-top: 30px
      .input-group
        margin-top: 30px
        &:nth-of-type(1)
          margin-top: 0
        .label
          font-weight: 500
        button
          display: block
          width: 100%
        &.check-group
          display: flex
          align-items: center
          justify-content: flex-start
          flex-wrap: wrap
          .label
            color: $txt-color
            font-size: 18px
            line-height: 25px
          .forgot-pass
            font-weight: 500
            color: $accent-color
            text-decoration: underline
            display: inline-block
            margin-left: auto
            &:hover
              text-decoration: none
  &.consultation-modal
    .consultation-form
      margin-top: 30px
      .input-group
        margin-top: 30px
        &:nth-of-type(1)
          margin-top: 0
        .label
          font-weight: 500
        button
          display: block
          width: 100%
  &.changepass-modal
    .changepass-form
      margin-top: 30px
      .input-group
        margin-top: 30px
        &:nth-of-type(1)
          margin-top: 0
        .label
          font-weight: 500
        button
          display: block
          width: 100%

  &.edit-modal
    .edit-form
      margin-top: 30px
      .input-group
        margin-top: 30px
        &:nth-of-type(1)
          margin-top: 0
        .label
          font-weight: 500
        button
          display: block
          width: 100%
      .input-row
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        margin-top: 30px
        .input-group
          flex: 0 0 calc(50% - 15px)
          max-width: calc(50% - 15px)
          margin-top: 0
          @media (max-width: 575px)
            flex: 0 0 100%
            max-width: 100%
          &:nth-of-type(2)
            margin-left: 30px
            @media (max-width: 575px)
              margin: 25px 0 0 0

