@import "../settings"
.section-title
  font-family: Manrope, sans-serif
  font-weight: 700
  font-size: 52px
  line-height: 119%
  @media (max-width: 1199px)
    font-size: 40px
    line-height: 110%
  @media (max-width: 1023px)
    font-size: 35px
  @media (max-width: 959px)
    font-size: 28px
  @media (max-width: 767px)
    font-size: 25px
  @media (max-width: 419px)
    font-size: 28px
.btn-border-style
  display: block
  width: 100%
  font-weight: 700
  font-size: 24px
  line-height: 133%
  text-align: center
  color: $accent-color
  border: 1px solid $accent-color
  border-radius: 12px
  transition: background-color .3s, color .3s, border-color .3s
  &:hover
    background-color: $accent-color
    color: #fff
    border-color: $accent-color
.title-row
  display: flex
  align-items: center
  justify-content: flex-start
  flex-wrap: wrap
  .descr-block
    color: $txt-gray-color
    font-size: 16px
    line-height: 22px
    margin: 0 0 0 auto
    max-width: 270px
    @media (max-width: 767px)
      flex: 0 0 100%
      max-width: 100%
      margin: 15px 0 0
.btn-style
  display: block
  color: #fff
  background-color: $accent-color
  border-radius: 8px
  font-weight: 700
  font-size: 21px
  line-height: 133%
  text-align: center
  padding: 15px
  transition: background-color .3s
  border: none
  @media (max-width: 575px)
    font-size: 18px
  &:hover
    background-color: $accent-hover-color
.info-block-title
  display: flex
  align-items: center
  justify-content: flex-start
  width: 100%
  color: $txt-gray-color
  font-weight: 500
  font-size: 16px
  line-height: 22px
  .custom-icon
    margin-right: 12px
.page-title
  font-family: Manrope, sans-serif
  font-weight: 700
  font-size: 52px
  line-height: 119%
  @media (max-width: 1199px)
    font-size: 45px
  @media (max-width: 1023px)
    font-size: 40px
  @media (max-width: 575px)
    font-size: 32px
  @media (max-width: 419px)
    font-size: 30px
.input-style
  display: block
  width: 100%
  font-weight: 400
  font-size: 16px
  line-height: 22px
  padding: 18px 12px
  border: 1px solid $border-gray-color
  background-color: transparent
  margin-top: 10px
  border-radius: 12px
  color: $txt-color
  transition: border-color .3s
  @media (max-width: 419px)
    padding: 12px
  &:focus
    border-color: $txt-color
  @include input-placeholder
    color: $txt-gray-color
.select-group
  position: relative
  .custom-icon
    position: absolute
    top: calc(50% - 12px)
    left: 16px
  .jq-selectbox
    position: relative
    display: block
    vertical-align: middle
    cursor: pointer
    z-index: 10
    transition: opacity .3s
    select
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      margin: 0
      padding: 0
      opacity: 0
    ul
      margin: 0
      padding: 0
    li
      -webkit-user-select: none
      -moz-user-select: none
      -ms-user-select: none
      user-select: none
      white-space: nowrap
      padding: 10px
      color: $txt-color
      &.selected
        background-color: #A3ABB1
        color: #FFF
      &:hover
        background-color: $accent-color
        color: #FFF
      &.disabled
        color: #AAA
        &:hover
          background: none
      &.optgroup
        font-weight: bold
        &:hover
          background: none
          color: #231F20
          cursor: default
      &.option
        padding-left: 25px
    &__select
      position: relative
      padding: 0 45px 0 52px
      font-weight: 500
      border: 1px solid $border-gray-color
      border-radius: 8px
      transition: border-color .3s
    &__select-text
      overflow: hidden
      -webkit-user-select: none
      -moz-user-select: none
      -ms-user-select: none
      user-select: none
      white-space: nowrap
      text-overflow: ellipsis
      display: block
      width: 100%
      color: $txt-gray-color
      transition: color .3s
      padding: 11px 0
      @media (max-width: 419px)
        font-size: 14px
        line-height: 20px
    &__dropdown
      position: absolute
      box-sizing: border-box
      width: 100%
      margin: 2px 0 0
      padding: 0
      border: 1px solid $border-gray-color
      border-radius: 4px
      overflow: hidden
      background: #FFF
      font-weight: 500
      font-size: 16px
      line-height: 22px
      ul
        position: relative
        overflow: auto
        overflow-x: hidden
        list-style: none
        -webkit-overflow-scrolling: touch
    &.focused .jq-selectbox__select
      border-color: $txt-color
    &.disabled
      opacity: .5
      cursor: default
    &__trigger
      position: absolute
      top: 0
      right: 0
      width: 42px
      height: 100%
    &__trigger-arrow
      &:before
        content: '\e80a'
        color: $accent-color
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        font-size: 12px
        line-height: 12px
        position: absolute
        top: calc(50% - 6px)
        right: calc(50% - 6px)
    &.changed
      .jq-selectbox__select-text
        color: $txt-color
.radio-group
  label
    display: block
    position: relative
    input
      position: absolute
      top: 0
      left: 0
      width: 0
      height: 0
      opacity: 0
      visibility: hidden
      &:checked
        & + .label
          &:before
            background-color: $accent-color
            color: #fff
            border-color: $accent-color
    .label
      font-weight: 600
      padding-left: 35px
      display: inline-block
      position: relative
      &:before
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: flex
        align-items: center
        justify-content: center
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        content: '\e800'
        width: 24px
        height: 24px
        font-size: 11px
        line-height: 11px
        border-radius: 50%
        background-color: transparent
        color: transparent
        border: 1px solid $txt-color
        position: absolute
        padding-top: 1px
        top: 0
        left: 0
        transition: color .3s, background-color .3s, border-color .3s
  .radio-descr
    margin-top: 10px
    font-weight: 500
    font-size: 16px
    line-height: 22px
    color: $txt-gray-color
    @media (max-width: 419px)
      font-size: 14px
      line-height: 20px
.textarea-group
  label
    display: block
    .label
      font-weight: 600
    textarea
      min-height: 160px
      @media (max-width: 419px)
        font-size: 14px
        line-height: 20px
.file-group
  label
    display: flex
    align-items: center
    justify-content: flex-start
    width: 100%
  .jq-file
    position: relative
    display: inline-block
    overflow: hidden
    width: 100%
    border-radius: 8px
    border: 1px solid $border-gray-color
    transition: border-color .3s
    cursor: pointer
    input
      position: absolute
      top: 0
      right: 0
      margin: 0
      padding: 0
      opacity: 0
      width: 0
      height: 0
      cursor: pointer
      z-index: 1
    &.focused
      border-color: $txt-color
    &.changed
      .jq-file__name
        color: $txt-color
      .icon.icon-file
        &:before
          content: '\e802'
    &:hover
      border-color: $txt-color

  .jq-file__name
    overflow: hidden
    white-space: nowrap
    text-overflow: ellipsis
    box-sizing: border-box
    width: 100%
    font-weight: 500
    font-size: 16px
    line-height: 22px
    padding: 13px 80px 13px 15px
    color: $txt-gray-color
    transition: color .3s
    cursor: pointer
  .jq-file__browse
    display: flex
    align-items: center
    justify-content: center
    position: absolute
    top: 0
    right: 0
    width: 54px
    height: 100%
    color: $accent-color
    font-size: 24px
    line-height: 24px
    cursor: pointer
.check-group
  label
    display: inline-flex
    align-items: flex-start
    justify-content: flex-start
    position: relative
    min-height: 24px
    input
      position: absolute
      top: 0
      left: 0
      width: 0
      height: 0
      opacity: 0
      visibility: hidden
      &:checked
        & + .label
          &:before
            background-color: $accent-color
            color: #fff
            border-color: $accent-color
    .label
      display: block
      padding-left: 35px
      color: $txt-gray-color
      font-weight: 400
      font-size: 16px
      line-height: 22px
      position: relative
      @media (max-width: 575px)
        font-size: 14px
      &:before
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: flex
        align-items: center
        justify-content: center
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        content: '\e800'
        width: 24px
        height: 24px
        font-size: 11px
        line-height: 11px
        border-radius: 4px
        background-color: transparent
        color: transparent
        border: 1px solid $txt-color
        position: absolute
        padding-top: 1px
        top: 0
        left: 0
        transition: color .3s, background-color .3s, border-color .3s

      a
        color: $accent-color
        text-decoration: underline
        &:hover
          text-decoration: none
.date-group
  display: block
  position: relative
  .custom-icon
    position: absolute
    top: calc(50% - 12px)
    left: 15px
  .input-style
    padding: 12px 12px 12px 50px
    font-weight: 500
    color: transparent
    border-radius: 8px
    -webkit-appearance: none
    transition: color .3s, border-color .3s
    cursor: pointer
    &:before
      content: attr(placeholder)
      color: $txt-gray-color
      position: absolute
      width: 100%
      max-width: calc(100% - 52px)
      background-color: #fff
      font-size: 18px
      line-height: 24px
      @media (max-width: 575px)
        font-size: 16px
      @media (max-width: 419px)
        font-size: 14px
    &.changed
      color: $txt-color
      &:before
        display: none
    &::-webkit-calendar-picker-indicator
      display: none
      -webkit-appearance: none

